(function(requireClass) {
  onmessage = function(e) {
    var process = e.data[0];

    if (!process) {
      return;
    }

    switch (process.task) {
      case 'SchemaForm':
      case 'ItemsForm':
        var asyncEditor = new Editor(process);
        break;
    }

    sendHtml(asyncEditor);
    close();
  };

  function sendHtml(asyncEditor) {
    postMessage(asyncEditor);
  }

})();

