(function() {
  this.App.directive('mnoeOrganizationsLocalList', ["$translate", "$filter", "$log", "toastr", "UserRoles", "MnoeUsers", function($translate, $filter, $log, toastr, UserRoles, MnoeUsers) {
    return {
      restrict: 'E',
      scope: {
        list: '=',
        user: '='
      },
      templateUrl: 'app/components/mnoe-organizations-local-list/mnoe-organizations-local-list.html',
      link: function(scope, elem, attrs) {
        var displayNormalState, setAllOrganizationsList, setLastOrganizationsList, setSearchOrganizationsList;
        scope.userContext = attrs.user != null;
        scope.userRoles = UserRoles;
        scope.organizations = {
          displayList: [],
          widgetTitle: 'mnoe_admin_panel.dashboard.organization.widget.local_list.loading_orgs.title',
          search: ''
        };
        setAllOrganizationsList = function() {
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.all_organizations.title';
          scope.organizations.switchLinkTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.all_organizations.switch_link_title';
          return scope.organizations.displayList = $filter('orderBy')(scope.list, 'email');
        };
        setLastOrganizationsList = function() {
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.last_organizations.title';
          scope.organizations.switchLinkTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.last_organizations.switch_link_title';
          scope.organizations.displayList = $filter('orderBy')(scope.list, '-created_at');
          return scope.organizations.displayList = $filter('limitTo')(scope.organizations.displayList, 10);
        };
        setSearchOrganizationsList = function() {
          var searchToLowerCase;
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.local_list.search_organizations.title';
          delete scope.organizations.switchLinkTitle;
          searchToLowerCase = scope.organizations.search.toLowerCase();
          scope.organizations.displayList = _.filter(scope.list, function(user) {
            var email, name, surname;
            if (user.email) {
              email = _.contains(user.email.toLowerCase(), searchToLowerCase);
            }
            if (user.name) {
              name = _.contains(user.name.toLowerCase(), searchToLowerCase);
            }
            if (user.surname) {
              surname = _.contains(user.surname.toLowerCase(), searchToLowerCase);
            }
            return email || name || surname;
          });
          return scope.organizations.displayList = $filter('orderBy')(scope.organizations.displayList, 'email');
        };
        displayNormalState = function() {
          if (attrs.view === 'all') {
            return setAllOrganizationsList();
          } else if (attrs.view === 'last') {
            return setLastOrganizationsList();
          } else {
            return $log.error('Value of attribute view can only be "all" or "last"');
          }
        };
        scope.switchState = function() {
          if (attrs.view === 'all') {
            attrs.view = 'last';
          } else {
            attrs.view = 'all';
          }
          return displayNormalState();
        };
        scope.searchChange = function() {
          if (scope.organizations.search === '') {
            return displayNormalState();
          } else {
            return setSearchOrganizationsList();
          }
        };
        scope.editRole = function(organization) {
          organization.beforeEditRole = organization.role;
          return organization.editMode = true;
        };
        scope.exitEditRole = function(organization) {
          organization.role = organization.beforeEditRole;
          return organization.editMode = false;
        };
        scope.updateUserRole = function(organization, user) {
          user.isUpdatingRole = true;
          user.role = organization.role;
          return MnoeUsers.updateUserRole(organization, user).then(function() {
            return $translate(UserRoles.keyFromRole(user.role)).then(function(tls) {
              return toastr.success('mnoe_admin_panel.dashboard.users.widget.local_list.role_update_success', {
                extraData: {
                  user: "" + user.email,
                  role: tls
                }
              });
            });
          }, function(error) {
            organization.role = organization.beforeEditRole;
            toastr.error('mnoe_admin_panel.dashboard.users.widget.local_list.role_update_error');
            return MnoErrorsHandler.processServerError(error);
          })["finally"](function() {
            organization.beforeEditRole = null;
            organization.isUpdatingRole = false;
            return organization.editMode = false;
          });
        };
        return scope.$watch('list', function(newVal) {
          if (newVal) {
            return displayNormalState();
          }
        }, true);
      }
    };
  }]);

}).call(this);

(function() {
  this.App.component('mnoeProductMarkupsList', {
    templateUrl: 'app/components/mnoe-product-markups-list/mnoe-product-markups-list.html',
    bindings: {
      view: '@',
      customerOrg: '<'
    },
    controller: ["$filter", "$log", "$translate", "MnoeProductMarkups", "MnoeCurrentUser", "MnoConfirm", "MnoeObservables", "OBS_KEYS", "toastr", function($filter, $log, $translate, MnoeProductMarkups, MnoeCurrentUser, MnoConfirm, MnoeObservables, OBS_KEYS, toastr) {
      var fetchProductMarkups, onMarkupAdded, onMarkupChanged, updateSearch, updateSort, vm;
      vm = this;
      vm.markups = {
        editmode: [],
        search: {},
        sort: "product.name",
        nbItems: 10,
        offset: 0,
        page: 1,
        list: [],
        widgetTitle: 'mnoe_admin_panel.dashboard.product_markups.widget.list.title',
        pageChangedCb: function(nbItems, page) {
          var offset;
          vm.markups.nbItems = nbItems;
          vm.markups.page = page;
          offset = (page - 1) * nbItems;
          return fetchProductMarkups(nbItems, offset);
        }
      };
      vm.readOnlyView = false;
      vm.callServer = function(tableState) {
        var search, sort;
        sort = updateSort(tableState.sort);
        search = updateSearch(tableState.search);
        return fetchProductMarkups(vm.markups.nbItems, vm.markups.offset, sort, search);
      };
      updateSort = function(sortState) {
        var sort;
        if (sortState == null) {
          sortState = {};
        }
        sort = "product.name";
        if (sortState.predicate) {
          sort = sortState.predicate;
          if (sortState.reverse) {
            sort += ".desc";
          } else {
            sort += ".asc";
          }
        }
        vm.markups.sort = sort;
        return sort;
      };
      updateSearch = function(searchingState) {
        var attr, ref, search, value;
        if (searchingState == null) {
          searchingState = {};
        }
        search = {};
        if (searchingState.predicateObject) {
          ref = searchingState.predicateObject;
          for (attr in ref) {
            value = ref[attr];
            if (_.isObject(value)) {
              search['where[' + attr + '.' + _.keys(value)[0] + '.like]'] = _.values(value)[0] + '%';
            } else {
              search['where[' + attr + '.like]'] = value + '%';
            }
          }
        }
        vm.markups.search = search;
        return search;
      };
      fetchProductMarkups = function(limit, offset, sort, search) {
        if (sort == null) {
          sort = vm.markups.sort;
        }
        if (search == null) {
          search = vm.markups.search;
        }
        vm.markups.loading = true;
        if (vm.customerOrg) {
          vm.readOnlyView = true;
          search['where[for_organization]'] = vm.customerOrg.id;
        }
        return MnoeProductMarkups.markups(limit, offset, sort, search).then(function(response) {
          vm.markups.totalItems = response.headers('x-total-count');
          return vm.markups.list = response.data;
        })["finally"](function() {
          return vm.markups.loading = false;
        });
      };
      vm.update = function(pm) {
        pm.isSaving = true;
        return MnoeProductMarkups.updateProductMarkup(pm).then(function(response) {
          updateSort();
          updateSearch();
          return delete vm.editmode[pm.id];
        }, function(error) {
          $log.error('Error while saving product markup', error);
          return toastr.error('mnoe_admin_panel.dashboard.product_markups.add_markup.modal.toastr_error');
        })["finally"](function() {
          return pm.isSaving = false;
        });
      };
      vm.remove = function(pm) {
        var modalOptions;
        modalOptions = {
          closeButtonText: 'mnoe_admin_panel.dashboard.product_markups.modal.remove_product_markup.cancel',
          actionButtonText: 'mnoe_admin_panel.dashboard.product_markups.modal.remove_product_markup.delete',
          headerText: 'mnoe_admin_panel.dashboard.product_markups.modal.remove_product_markup.proceed',
          bodyText: 'mnoe_admin_panel.dashboard.product_markups.modal.remove_product_markup.perform'
        };
        return MnoConfirm.showModal(modalOptions).then(function() {
          pm.isSaving = true;
          return MnoeProductMarkups.deleteProductMarkup(pm).then(function() {
            updateSort();
            updateSearch();
            return toastr.success('mnoe_admin_panel.dashboard.product_markups.modal.remove_product_markup.toastr_success');
          })["finally"](function() {
            return pm.isSaving = false;
          });
        });
      };
      vm.showOrgName = function(name) {
        if (!name) {
          return $translate.instant("mnoe_admin_panel.dashboard.product_markups.add_markup.modal.all_companies");
        }
        if (vm.readOnlyView) {
          return $translate.instant("mnoe_admin_panel.dashboard.product_markups.add_markup.modal.customer_specific");
        } else {
          return name;
        }
      };
      vm.customerHeader = function() {
        if (vm.readOnlyView) {
          return "mnoe_admin_panel.dashboard.product_markups.widget.list.table.markup_type";
        } else {
          return "mnoe_admin_panel.dashboard.product_markups.widget.list.table.customer";
        }
      };
      onMarkupAdded = function() {
        return fetchProductMarkups(vm.markups.nbItems, vm.markups.offset);
      };
      onMarkupChanged = function() {
        return fetchProductMarkups(vm.markups.nbItems, vm.markups.offset);
      };
      MnoeObservables.registerCb(OBS_KEYS.markupAdded, onMarkupAdded);
      MnoeObservables.registerCb(OBS_KEYS.markupChanged, onMarkupChanged);
      this.$onDestroy = function() {
        MnoeObservables.unsubscribe(OBS_KEYS.markupAdded, onMarkupAdded);
        return MnoeObservables.unsubscribe(OBS_KEYS.markupChanged, onMarkupChanged);
      };
    }]
  });

}).call(this);

